import React from "react";
import IdeationContent from "./ideationContent";
import IdeationExperienceMap from "./img/ideation-experience-map.png";
import IdeationSketching from "./img/ideation-sketching.png";

function IdeationSection() {
	return (
		<section className="content-block" id={IdeationContent.meta}>
			<h4 className="section-title-meta"> {IdeationContent.meta} </h4>
			<h3 className="section-title"> {IdeationContent.title} </h3>
			{IdeationContent.paras.map((p) => {
				return <p>{p}</p>;
			})}
			<figure className="image">
				<img src={IdeationExperienceMap} alt={IdeationContent.img01alt} />
				<figcaption>{IdeationContent.img01caption}</figcaption>
			</figure>
			<figure className="image">
				<img src={IdeationSketching} alt={IdeationContent.img02alt} />
				<figcaption>{IdeationContent.img02caption}</figcaption>
			</figure>
		</section>
	);
}

export default IdeationSection;
